import { useState } from "react";
import Header from "./components/Header";
import Tasks from "./components/Tasks";
import AddTask from "./components/AddTask";

function App() {

  const [tasks, setTasks] = useState([
    {
      id: 1,
      text: 'Learn React',
      day: 'Saturday',
      reminder: false
    },
    {
      id: 2,
      text: 'Watch a movie with bee',
      day: 'Sunday',
      reminder: false
    },
    {
      id: 3,
      text: 'Wash my hair',
      day: 'Sunday',
      reminder: true
    }
  ])

  const [showTasks, setShowTasks] = useState(false)

  //Delete Task
  const deleteTask = (id) => {
    setTasks(tasks.filter((task) => task.id !== id))
  }

  //Show Reminder
  const showReminder = (id) => {
    setTasks(tasks.map((task) => task.id === id ? {...task, reminder: !task.reminder} : task))
  }
  
  //Add Task
  const addTask = (task) => {
    const id = Math.floor(Math.random() * 1000)
    const newTask = {id, ...task}
    setTasks([...tasks, newTask])
  }

  return (
    <div className="container">
      <Header show={showTasks} open={() => setShowTasks(!showTasks)}/>
      {showTasks && <AddTask carryState={addTask}/>}
      {tasks.length > 0 ? <Tasks tasks={tasks} deleteTask={deleteTask} showReminder={showReminder}/>: 'No tasks to show.'}
    </div>
  );
}

export default App;